.skills {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    max-width: 1200px;
    padding:20px;
}

.skills .title h1{
    margin-top: 20px;
   color: #E1E8ED;
   margin-left: 10px;
}

.skills .content{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    flex-wrap: wrap;
    padding: 10px;
}

.skills .content .bg-bar {
    position: relative;
    background:#E1E8ED ;
    height: 10px;
    width: 500px;
    border-radius: 6px;
}

.skills .content .bg-bar .level {
    background: #1DA1F2;
    height: 100%;
    border-radius: 6px 0px 0px 6px;
}

.skills .bg-bar .level span {
    position: absolute;
    top: -20px;
    font-size: 16px;
    font-weight: 400;
    color:  #F5F8FA;
}

@media screen and (max-width: 950px) {
    .skills .content{
        padding: 5px 20px;
    }
    .skills .content .bg-bar {
        width: 300px;
    }
    .skills .title h1{
        margin-left: 17px;
     }
  }