
.app {
    position: relative;
    top: 0;
    left: 0;
    overflow: hidden;
    letter-spacing: 1px;
}

.about-skill {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 50px;
    background: #1d344b;
    border-top: 2px solid #1DA1F2;

}
.app .service-sec {
    position: relative;
    background: #F5F8FA;
    display: flex;
    justify-content: center;
    border-bottom: 3px solid #1DA1F2;
}

#tsparticles {
    position: fixed;
    background: #1d344b;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
}
