.header {
  position: relative;
  height: 100vh;  
  box-sizing: border-box;
  padding: 0;
  display: flex;
  justify-content: center;
  color: #F5F8FA ;
}

.header .content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header .content h1 {
  font-size: 42px;
  font-weight: bold;
}

.header .content h1 span{
  color: #1DA1F2;
}


.header .content p {
  font-size: 32px;
  font-weight: 500;
}

.header .content p span{
  color: #1DA1F2;
  font-size: 38px;
}
.header .content p #point{
  font-size: 50px;
}

.header .content button{
  font-size: 24px;
  font-weight: bold;
  background: #1DA1F2;
  color: #F5F8FA;
  padding: 8px 16px;
}
.header .content button:hover{
  opacity: 0.8;
}

@media screen and (max-width: 950px) {
  .header{
    height: 100vh;
   background: none;
  }
  .header .content h1{
    font-size: 23px;
  }
  .header .content p{
    font-size: 17px;
  }
}

