.about {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px;
    color: #F5F8FA;
    max-width: 1200px;
    border-bottom: 2px solid #F5F8FA;
}

.about .title {
    margin-top: 40px;

}

.about .description p{
    font-size: 22px;
    font-weight: 400;
    color:  #F5F8FA;
 }

.about button {
    font-size: 18px;
    font-weight: bold;
    background: #1DA1F2;
    color: #F5F8FA;
    padding: 8px 16px;
}
