.contact {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 30px;
    color: #000;
    background: #fff;
    border-top: 2px solid #1DA1F2;


}
.contact-sec {
    position: relative;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1500px;
    padding: 30px;
    gap: 60px;
}

.contact-sec .personel-info {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    height: 400px;
    width: 400px;
    gap: 10px;
    padding: 20px;
    border-radius: 10px;
}
.contact-sec .personel-info .name {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}
.contact-sec .personel-info .name span{
    font-size: 34px;
    color: #1DA1F2;
    margin-top:15px;
    padding: 0px 20px;
}

.contact-sec .contact-sec-form {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
}

.contact-sec .contact-form .notif {
    color: green;
    font-size: 16px;
}
.contact-sec .contact-form .input-field{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 5px;
    font-size: 21px;
    padding: 5px;
}

.contact-sec .contact-form .input-field .btn{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.contact-sec .contact-form .input-field input{
    padding: 5px;
    width: 400px;
    height: 30px;
    border-radius: 5px;
    font-size: 24px;
    border: 1px solid gray ;
}
.contact-sec .contact-form .input-field input:focus{
    border: 2px solid #1DA1F2;
}
.contact-sec .contact-form .input-field textarea{
    padding: 5px;
    width: 400px;
    border-radius: 5px;
    height: 150px;
    font-size: 20px;
    border: 1px solid gray;
}

.contact-sec .contact-form .input-field textarea:focus{
    border: 2px solid #1DA1F2;
}

.contact-sec .contact-form .input-field button{
    width: 100px;
    height: 36px;
    font-size: 26px;
    font-weight: 600;
}


@media screen and (max-width: 950px) {
    .contact-sec .personel-info {
        width: 320px;
    }
    .contact-sec .contact-form .input-field input{
        width: 320px;
    }
    .contact-sec .contact-form .input-field textarea {
        width: 320px;
    }
  }