.service {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px;
    color: #000;
    gap: 20px;
}

.service .title {
    margin-top: 50px;
}

.service .service-cards {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
}
.service .service-cards .content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px;
    width: 200px;
}

.service .service-cards .content .icon {
    font-size: 60px;
    color: #1DA1F2;
}

.service .service-cards .content .title h1{
    font-size: 32px;
    color: #666;
}

.service .service-cards .content .paragraph p{
    font-size: 21px;
    font-weight: 300;
}