.portfolio {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px;
    color: #000;
    gap: 30px;
    background: #F5F8FA;
}
.portfolio .title {
    margin-top: 20px;
}
.portfolio .cards {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
}
.portfolio .cards .content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 40px;
    width: 400px;
    gap: 5px;
    border: 1px solid #111;
    border-radius: 5px;
}

.portfolio .cards .content .title h1{
    position: absolute;
    top: 5px;
    left: 5px;
    font-size: 20px;
    font-weight: bold;
    color:  #1DA1F2;
}

.portfolio .cards .content .paragraph p{
    font-size: 17px;
    font-weight: 400;
}

.portfolio .cards .content .uses{
   position: absolute;
   right: 5px;
   bottom: 5px;
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 2px;
}

.portfolio .cards .content .uses span{
   padding: 2px;
   font-size: 14px;
   font-weight: 700;
   border: 1px solid #444;
   border-radius: 3px; 
}

@media screen and (max-width: 950px) {
    .portfolio .cards .content {
        width: 270px;
    }
  }